.card-tag-top {
  background-color: $white;
  color: #000;
  padding-top: 18px;
  padding-bottom: 18px;
  padding-left: 10px;
  padding-right: 10px;
  box-shadow: -4px 4px 4px 4px rgba(0, 0, 0, 0.25);

  writing-mode: vertical-rl;
  text-orientation: upright;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateY(-5%);
}

.card-hover-shadow-1 {
  transition: all 0.3s linear;
  border-radius: 10px;

  &:hover {
    & > img {
      box-shadow: 6px 6px 7px 0 rgba(0, 0, 0, 0.25);
      border-radius: 15px;
    }
    & > p {
      color: $gray-700;
    }
  }

  @include media-breakpoint-up(md) {
    &:hover {
      & > img {
        box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, 0.25);
      }
    }
  }
}
.card-hover-shadow-2 {
  transition: all 0.2s linear;
  &:hover {
    box-shadow: 6px 6px 7px 0 rgba(0, 0, 0, 0.25);
  }
  @include media-breakpoint-up(md) {
    &:hover {
      box-shadow: 12px 12px 10px 0 rgba(0, 0, 0, 0.25);
    }
  }
}
.card-shadow {
  box-shadow: 0 4px 10px 0 rgba(251, 251, 251, 0.25);
}
.card {
  border-radius: 30px;
}

.card-rounded {
  border: 0;
  border-radius: 30px;
}
.card-rounded-top {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.card-rounded-bottom {
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}
.card-rounded-start {
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}
.card-rounded-end {
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
}
.card-rounded-bottom-start {
  border-bottom-left-radius: 30px;
}

.card-rounded-bottom-end {
  border-bottom-right-radius: 30px;
}

.card-bg-img {
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.card-border {
  border: 2px solid rgba(0, 0, 0, 0.3);
}
.card-btn-group {
  background-color: $white;
  border-radius: 20px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;

  display: flex;
  align-items: center;
  gap: 4px;
}

.card-btn-circle {
  background-color: $white;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-top-start {
  position: absolute;
  top: 20px;
  left: 20px;
}
.card-top-end {
  position: absolute;
  top: 20px;
  right: 20px;
}

.link-star {
  color: #f5ce42;
}

.card-hover-filter {
  position: relative;
  transition: all 0.3s linear;
  &:hover::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: $gray-600;
    border-radius: 30px;
    z-index: 1200;
    vertical-align: middle;
    margin-bottom: 0;
    padding-bottom: 0;
    // transform: rotate(0.04deg);
  }
}
.card-hover-filter-sm{
    &:hover::before {
        border-radius: 20px;
    }
}
.card-hover-filter-link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: 2200;
}

.card-hover-filter-start {
  &:hover::before {
    border-radius: 0;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
  }
}
.card-hover-filter-middle {
  &:hover::before {
    border-radius: 0;
  }
}

.card-hover-filter-end {
  &:hover::before {
    border-radius: 0;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
  }
}

.card-light-filter {
  background-color: rgba(251, 251, 251, 0.4);
}

.card-hover-icon {
  opacity: 0;
  z-index: 1201;
  transition: transform 0.3s cubic-bezier(0.05, 1.43, 0.66, -1.46), opacity 0.2s;
  transform: scale(0.85);
  &:hover {
    opacity: 1;
    transform: scale(1);
  }
}
