ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.h7,
.h8 {
  margin-bottom: 0;
}

a {
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  display: block;
}

button {
  padding: 0;
}

input {
  &:focus {
    outline: none;
  }
  &:active {
    outline: none;
  }
}
